.pay-table {
	margin-bottom: rem(20);

	width: rem(468);
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	@include min660 {
		width: 100%;
		justify-content: flex-end;
		flex-direction: row-reverse;
	}

	
}

.pay-system_table {
	display: flex;
	flex-direction: column;

	.button-blue {
		margin-top: 0 !important;

		@include min660 {
			margin-top: rem(20) !important;
		}
	}

	@include min660 {
		// width: 100%;
		// margin-bottom: rem(20);


		> .flex {
			flex-direction: column;
		}

		&:nth-child(2) {
			margin-right: rem(20);
			margin-bottom: 0;
		}

		&:last-of-type {
			width: 100%;
		}
	}

	b {
		margin-bottom: rem(5);
	}

	a {
		margin-left: rem(10);
		color: $blue2;

		@include min660 {
			margin-left: 0;
		}
	}
}